.subpage-header{
    position: relative;
    z-index: 1;
  }
  .subpage-banner{
    height: 250px;
    width: 100%;
    min-width: 1200px;
    object-fit: cover;
  }
  .subpage-title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }